@import 'element';
@import 'mixin';


.poker .player {}

.poker .player-position-wait { @include position-top-right($margin-tiny); width:150px; height:150px;  background-color: $color-grey;}
.poker .player-position-join { @include position-cover;  background-color: $color-grey;}
.poker .player-me { background-color: $color-red; }
.poker .player .profile-img { width:50px; height:50px }
.poker .player .profile-data { @include font-default($color-white) }
.poker .player .play-data { @include font-default($color-white) }
.poker .player .bankroll { @include font-default($color-white) }
.poker .player .status { @include font-default($color-blue) }
.poker .player .time-bar { width:0%; height:15px;  background-color: $color-blue; }

.poker .position { position: absolute; background-color: $color-grey; width:150px; height:150px; @include bg-radius(20px) }
.poker .position .btn-join { @include btn-text; @include btn-icon-size }

.poker .game-viewer { @include position-cover; }
.poker .game-viewer .card{ @include position-top-left($margin-tiny); width:100px; height:200px;  background-color: $color-grey; }
.poker .game-viewer .ante{ @include position-center(100,50, 0, -100); @include font-default($color-white)  }
.poker .game-viewer .mainPot{@include position-center(200,50); @include font-big($color-white) }
.poker .game-viewer .sidePot{@include position-center(200,50, 0, 100); @include font-default($color-white) }

.poker .player-viewer { @include position-cover; }
.poker .ui-box { @include position-bottom; width:100%; height:100px; background-color: $color-white}

.poker .ui-box .player-info { @include font-default($color-white) }
.poker .ui-box .game-info { @include font-default($color-white) }
.poker .ui-box .btn-blind { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-fold { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-small-blind { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-big-blind { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-check { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-call { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-bat { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-raise { @include btn-text; @include btn-icon-size }
.poker .ui-box .btn-allin { @include btn-text; @include btn-icon-size }
